export enum Days {
  Monday = 'monday',
  Tuesday = 'tuesday',
  Wednesday = 'wednesday',
  Thursday = 'thursday',
  Friday = 'friday',
  Saturday = 'saturday',
  Sunday = 'sunday',
}

export type DayPlan = {
  day: Days;
  meal: string;
};

export type WeekPlan = {
  week: number;
  plans: DayPlan[];
};

export class MealPlanService {
  public mealPlan(week: number) {
    const plans: WeekPlan[] = [];

    plans.push({
      week: 50,
      plans: [
        {
          day: Days.Tuesday,
          meal: 'Tomatsuppe',
        },
        this.generate(Days.Wednesday, 'Stegte nudler'),
        this.generate(Days.Thursday, 'Hjemmelavet burger'),
      ],
    });

    plans.push({
      week: 51,
      plans: [
        this.generate(Days.Monday, 'Mørbrad'),
        this.generate(Days.Tuesday, 'Købe Pizza'),
        this.generate(Days.Wednesday, 'Hamburgerryg/Skinke med flødekartofler'),
        this.generate(Days.Thursday, 'Forslag modtages gerne'),
      ],
    });

    plans.push({
      week: 52,
      plans: [this.generate(Days.Monday, 'Pita med kylling og oksekød')],
    });

    plans.push({
      week: 1,
      plans: [
        this.generate(
          Days.Monday,
          'Ovenstegte grønsager med ris og pandestegt laks',
        ),
      ],
    });

    return plans.find((plan) => plan.week === week);
  }

  private generate(day: Days, meal: string): DayPlan {
    return {
      day,
      meal,
    };
  }
}

export const mealPlanService = new MealPlanService();
